body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

header * {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 0.15rem 0.75rem 0 rgba(0, 0, 0, 0.15);
  background-color: white;
  height: 70px;
  z-index: 10;
}

header .listItem a:active {
  color: black;
}

header .listItem a:visited {
  color: black;
}

header .subContainer span {
  color: #35bfc4;
}

.headerMenu {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  max-width: 1200px;
}

.logoContainer a {
  font-size: 20px;
  font-weight: 600;
  color: blue;
  word-spacing: -2px;
}

header > .logoContainer {
  display: none;
}

.logoContainer.Ismobile {
  display: block;
  width: 100%;
  background-color: white;
}

.logoContainer.Ismobile a {
  padding: 22px;
  display: block;
}

.listItem {
  display: flex;
  list-style: none;
  position: relative;
}

.listItem a {
  display: block;
  padding: 15px 20px 15px 20px;
}

.listItem a i {
  padding-left: 8px;
}

.listItem a:hover {
  text-decoration: underline;
}

.subContainer {
  position: absolute;
  display: flex;
  background-color: #f8fafb;
  top: 50px;
  left: -130px;
  padding: 20px 0;
  border-right: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  border-left: 1px solid #ddd;
  border-radius: 0 0 4px 4px;
  box-shadow: 0 2px 42px 0 rgba(0, 0, 0, 0.16);
  display: none;
}

.subContainer .subListContainer {
  border-right: 1px solid #e3e3e3;
  padding-left: 10px;
  min-width: 200px;
}

.subContainer .subListContainer ul {
  list-style: none;
}

.subContainer .subListContainer ul a {
  display: block;
  padding: 15px 20px 15px 20px;
  white-space: nowrap;
  min-height: 21px;
  border: 1px solid transparent;
}

.subContainer .subListContainer ul a:hover {
  text-decoration: underline;
  border: 1px solid black;
}

.subContainer.lastLink {
  top: 50px;
  left: 250px;
}

.item:hover .subContainer {
  display: flex;
}

.headerBurger {
  display: none;
}

.logoContainer {
  z-index: 1;
  background-color: white;
}

@media (max-width: 769px) {
  header {
    height: 70px;
  }
  .subContainer {
    position: static;
  }
  .headerBurger {
    display: block;
    width: 35px;
    height: 15px;
    position: relative;
    margin-right: -35px;
    /* top: 0px; */
    left: -75px;
    z-index: 2;
  }
  .headerBurger:before,
  .headerBurger:after {
    padding: 0;
    margin: 0;
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    height: 3px;
    background-color: #2d3cc2;
    transition: all 0.3s ease 0s;
  }
  .headerBurger:before {
    top: 0;
  }
  .headerBurger:after {
    bottom: 0;
  }
  .headerBurger.true:before {
    transform: rotate(45deg);
    top: 6px;
  }
  .headerBurger.true:after {
    transform: rotate(-45deg);
    bottom: 6px;
  }
  .headerBurger.true span {
    transform: scale(0);
  }
  .headerBurger span {
    position: absolute;
    width: 100%;
    height: 3px;
    background-color: #2d3cc2;
    top: 6px;
    left: 0;
    transition: all 0.3s ease 0s;
  }
  .listItem {
    display: block;
  }
  .listItem li {
    margin: 0px 0px 20px 10px;
  }
  .headerMenu {
    position: fixed;
    top: 0;
    transform: translateY(-110%);
    width: 100%;
    height: 100%;
    background-color: white;
    padding: 60px 0 0 0;
    transition: all 0.3s ease 0s;
    overflow: auto;
    box-sizing: border-box;
  }
  .headerMenu.true {
    transform: translateY(0%);
    display: block;
  }
  .headerMenu.true .subContainer {
    display: none;
  }
  .headerMenu.true .logoContainer {
    display: none;
    position: absolute;
    top: 30px;
    left: 35px;
  }
  .headerMenu.true li {
    margin: 0;
    padding: 0;
  }
  .headerMenu.true li .a {
    padding: 10px 20px 10px 20px;
  }
  .headerMenu.true .item.true .subContainer {
    display: flex;
    flex-wrap: wrap;
    top: 140px;
    left: 30px;
  }
  .headerMenu.true .item.true .subContainer .subListContainer {
    border: none;
    width: 180px;
  }
  .headerMenu.true .containerMail {
    display: none;
  }
  .headerMenu.true .listItem {
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 40px;
  }
  .headerMenu.true .headerBurger:before,
  .headerMenu.true .headerBurger:after {
    left: 30px;
  }
  header {
    justify-content: space-between;
  }
}

.content * {
  margin: 0;
}

.content {
  margin-top: 85px;
}

.titleContainer {
  text-align: center;
}

.titleContainer h1 {
  padding: 15px 0;
  font-weight: 600;
}

.titleContainer p {
  padding-bottom: 30px;
}

.buttonsContainer {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.buttonsContainer a {
  width: 300px;
  height: 40px;
  background-color: white;
  color: black;
  border: none;
  border: 1px solid #e3e3e3;
  border-radius: 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  margin: 10px 0;
  box-shadow: 0px 1px 3px 0 #ddd;
  transition: box-shadow 200ms;
}

.buttonsContainer a span {
  -webkit-transition: -webkit-transform 360ms;
  transition: transform 360ms;
}

.buttonsContainer a:hover {
  box-shadow: 0px 5px 13px 0 #ddd;
}

.buttonsContainer a:hover span {
  -webkit-transform: translateX(30%);
  transform: translateX(30%);
}

.newspaperContainer {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding: 30px 0;
  justify-content: space-between;
}

.newspaperContainer img {
  padding: 10px 20px;
}

.likeShareContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 30%;
  margin: 0 auto;
}

.likeShareContainer a {
  display: block;
  width: 150px;
  height: 30px;
  border-radius: 5px;
  background-color: #286b97;
  color: white;
  font-size: 18px;
  text-align: center;
  padding-top: 5px;
  margin-left: 10px;
}

.likeShareContainer p {
  font-size: 12px;
  padding-left: 5px;
}

@media (max-width: 680px) {
  .buttonsContainer {
    justify-content: center;
  }
}

@media (max-width: 1025px) {
  .buttonsContainer {
    justify-content: center;
  }
  .buttonsContainer a {
    margin: 10px 15px;
  }
}

@media (max-width: 425px) {
  .buttonsContainer a {
    margin: 8px 10px;
  }
  .newspaperContainer {
    flex-direction: column;
  }
  .likeShareContainer {
    flex-direction: column;
  }
  .likeShareContainer a {
    margin: 5px 0;
  }
}

@media (max-width: 769px) {
  .content {
    padding-top: 45px;
  }
  .likeShareContainer {
    flex-direction: column;
  }
  .likeShareContainer a {
    margin: 5px 0;
  }
}

h1 {
  text-align: center;
  font-weight: 600;
}

.featureContainer * {
  padding: 0;
  margin: 0;
}

.featureContainer {
  margin: 40px 0;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16), 0 3px 6px 0 rgba(0, 0, 0, 0.23);
  border-radius: 5px;
}

.featureContainer .imgContainer {
  margin: 10px;
}

.featureContainer .dataContainer {
  padding: 0 15px;
}

.featureContainer .dataContainer a {
  color: blue;
  font-weight: 600;
  padding-top: 10px;
  display: block;
  width: 100px;
  height: 30px;
}

.featureContainer .evaluationContainer {
  display: flex;
}

.featureContainer .evaluationContainer p {
  padding: 20px 0;
}

.featureContainer .evaluationContainer .star-ratings {
  width: 137px;
  height: 30px;
  padding-top: 5px;
  padding: 10px 10px 0px 10px;
}

.featureContainer .evaluationContainer .starsContainer {
  margin-top: 20px;
  padding: 0 10px;
}

.featureContainer .evaluationContainer .starsContainer.two #star1,
.featureContainer .evaluationContainer .starsContainer.two #star2 {
  color: yellow;
}

.featureContainer .evaluationContainer .starsContainer.three #star1,
.featureContainer .evaluationContainer .starsContainer.three #star2,
.featureContainer .evaluationContainer .starsContainer.three #star3 {
  color: yellow;
}

.featureContainer .evaluationContainer .starsContainer.four #star1,
.featureContainer .evaluationContainer .starsContainer.four #star2,
.featureContainer .evaluationContainer .starsContainer.four #star3,
.featureContainer .evaluationContainer .starsContainer.four #star4 {
  color: yellow;
}

.featureContainer .evaluationContainer .starsContainer.four #star1,
.featureContainer .evaluationContainer .starsContainer.four #star2,
.featureContainer .evaluationContainer .starsContainer.four #star3,
.featureContainer .evaluationContainer .starsContainer.four #star4 {
  color: yellow;
}

.featureContainer .buttonContainer a {
  display: block;
  background-color: blue;
  width: 195px;
  height: 40px;
  color: white;
  font-weight: 600;
  font-size: 20px;
  border-radius: 5px;
  margin: 10px 0;
  margin-right: 10px;
  padding-top: 12px;
  text-align: center;
  border: 1px solid transparent;
}

.featureContainer .buttonContainer .detailsButton {
  color: blue;
  background-color: white;
}

.featureContainer .buttonContainer a:hover {
  border: 1px solid blue;
}

.linkContainer a {
  display: block;
  text-decoration: none;
  color: blue;
  font-size: 30px;
  text-align: center;
  padding-bottom: 30px;
}

.botomContent {
  display: flex;
  padding: 10px;
}

.botomContent .imgContainer {
  padding-right: 30px;
}

.botomContent .facebookContainer {
  padding-left: 10px;
}

#textContainerTitle {
  margin-top: 0;
}

footer {
  padding: 10px;
  display: flex;
  background-color: #f8fafb;
  justify-content: center;
}

footer .col {
  margin: 10px;
  margin-right: 50px;
}

footer h2,
footer h3,
footer h4,
footer ul {
  padding: 0;
  margin: 0;
}

footer ul {
  list-style: none;
}

footer ul li {
  padding: 5px;
}

footer h2,
footer h3 {
  padding: 10px 0;
  padding-top: 0;
}

footer h4 {
  padding-bottom: 12px;
}

footer .col1 {
  max-width: 25%;
}

footer .col1 a:active {
  color: black;
}

footer .col1 a:visited {
  color: black;
}

footer .col4 a {
  font-size: 30px;
  padding: 5px;
}

footer .logoContainer {
  background-color: #f8fafb;
}

@media (max-width: 426px) {
  .featureContainer .imgContainer {
    text-align: center;
  }
  footer {
    flex-direction: column;
    justify-content: start;
    align-items: start;
    flex-wrap: nowrap;
  }
  footer div {
    padding-left: 10px;
  }
  footer .col1 {
    max-width: 100%;
  }
  footer .col4 {
    margin-bottom: 20px;
  }
  .dataContainer h2 {
    padding-bottom: 10px;
    text-align: center;
  }
  .evaluationContainer {
    display: block !important;
  }
  .evaluationContainer p {
    padding: 5px 0 !important;
    margin: 0 !important;
    text-align: center !important;
  }
  .evaluationContainer .starsContainer {
    margin: 0 !important;
    text-align: center !important;
    padding: 5px 0 !important;
  }
}

@media (max-width: 1025px) {
  .features {
    padding: 10px;
  }
  .featureContainer {
    flex-direction: column;
    margin-top: 30px;
  }
  .featureContainer .buttonContainer a {
    margin: 10px auto;
  }
  .featureContainer .dataContainer a {
    float: right;
  }
  .botomContent {
    display: block;
  }
  .botomContent .imgContainer {
    text-align: center;
  }
  #textContainerTitle {
    margin-top: 30px;
  }
}
